.desc {
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    margin-bottom: -70px;
  }
  
#grid {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    line-height: 0;
}
#grid img {
    height: 100vh;
    margin-top: 3px;
    width: 100% !important;
    height: auto !important;
}
#grid img:hover {
    background: black;
    opacity: 1;
    z-index:999999;
    transition: 0.5s ease;
}           
 
  /* special shoutout to my iphone 5 bitches */
  @media screen and (max-width: 700px) {
    .desc, img {
       width: 90vw !important;
    }
    #grid {
        width: 100vw important;
        height: auto !important;
        -webkit-column-count: 2;
        -moz-column-count: 2;
        -moz-column-gap: 3px;
        column-count: 2;
     }
     #grid img {
      /** width: 100vh; **/
        height: auto !important;
        width: 100vw;
     }
  } 
  @media only screen and (min-width: 700px) {
    .desc {
      width: 70%;
    }
    #grid {
        width: 75%;
        -webkit-column-count: 4;
        -webkit-column-gap: 3px;
        -moz-column-count: 4;
        -moz-column-gap: 3px;
        column-count: 4;
        column-gap: 3px;
    }
  }
  
